import React from 'react';
import styles from './styles/productProps.module.scss';
import getProductProperties from './api/getProperties';

const ProductProperties = () => {
    const [properties, setProperties] = React.useState<any>();
    const productId = +window.location.href.split('/')[4].slice(1);

    const tableData = [
        ["", properties?.bike_color],
        ["", properties?.bike_size],
        [" ", properties?.bike_year],
        [" ", properties?.brake_system],
        [" ", properties?.brake_type],
        [" ", properties?.connecting_rod],
        [" ", properties?.country],
        ["", properties?.fork],
        ["", properties?.frame],
        [" ", properties?.frame_material],
        ["", properties?.guarantee],
        ["", properties?.manufacturer],
        [" ", properties?.rear_derailleur],
        ["", properties?.saddle],
        [" ", properties?.seatpost],
        ["", properties?.shifters],
        [" ", properties?.speeds_count],
        ["", properties?.takeaway],
        ["", properties?.tires],
        [" ", properties?.wheel_diametr],
        [" ", properties?.wheels_manufacturer],
    ]

    React.useEffect(() => {
        getProductProperties(productId).then((data:any) => setProperties(data[0]))
    }, [])
    
    return (
        <div className={styles.block}>
            <div className='wrapper'>
                <h2 className={styles.ttl}></h2>
                <ul>
                    {tableData.map((elem: any, index: number) => {
                        return (
                            <li className={index++ % 2 === 0 ? styles.listItem : styles.whiteListItem} key={elem[0]}><span>{elem[0]}</span><span>{elem[1]}</span></li>
                        )
                    })}
                </ul>
            </div>
        </div>
    );
};

export default ProductProperties;